ul.table-of-contents-list.design_pattern li.active > span, ol.table-of-contents-list.design_pattern li.active > span,
ul.table-of-contents-list.design_pattern li.active::before, ol.table-of-contents-list.design_pattern li.active::before {
    color: color(#3eb0ef lightness(-20%));
}

ul.table-of-contents-list.design_pattern > li:before,
ol.table-of-contents-list.design_pattern > li:before {
    content: '\f067';
}

ul.table-of-contents-list.design_pattern > li > ul.table-of-contents-list > li {
    padding: 0;
    margin: 0 0 0 20px;
}

ul.table-of-contents-list.design_pattern > li > ul.table-of-contents-list > li:before,
ol.table-of-contents-list.design_pattern > li > ul.table-of-contents-list > li.second:before {
    content: '\f0da';
}

ul.table-of-contents-list.design_pattern li.none::before, ol.table-of-contents-list.design_pattern li.none::before  {
    color: orangered;
}

ul.table-of-contents-list.design_pattern > li > ul.table-of-contents-list > li > a[href="#"] {
    color: orangered;
    -webkit-box-shadow: none;
    box-shadow: none;
    border-bottom: 1px dashed orangered;
}

ul.table-of-contents-list.design_pattern > li > ul.table-of-contents-list > li > a[href="#"]:hover {
    color: color(orangered whiteness(25%));
    border-bottom-color: color(orangered whiteness(25%));
}